@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}
.body {
  width: 100%;
  position: relative;
  min-height: 500px;
}

/* Navbar */
.navbar-logo {
  width: 100px;
  margin-left: 20px;
  display: none;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 50vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  display: flex;
  align-items: center;
  height: 80px;
}

.nav-links {
  color: #dfa92e;
  text-decoration: none;
  padding: 0.5rem 1rem;
}
.nav-links:hover {
  color: white;
  transition: .4s;
  cursor: pointer;
}

.nav-links-mobile {
  display: none;
}

.menu-btn {
  position: relative;
  justify-content: center;
  display: none;
  align-items: center;
  height: 80px;
  width: 80px;
  cursor: pointer;
  transition: all .5s ease-in-out;
  /* border: 3px solid white; */
}
.menu-btn_burger {
  height: 4px;
  width: 40px;
  background: #dfa92e;
  border-radius: 5px;
  transition: all .5s ease-in-out;
}
.menu-btn_burger::before,
.menu-btn_burger::after {
  content: " ";
  position: absolute;
  width: 40px;
  height: 4px;
  background: #dfa92e;;
  border-radius: 5px;
  transition: all .5s ease-in-out;
}
.menu-btn_burger::before {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}
.menu-btn_burger::after {
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
}

/* Animation */
.menu-btn.open .menu-btn_burger {
  -webkit-transform: translateX(-50px);
          transform: translateX(-50px);
  background: transparent;
}
.menu-btn.open .menu-btn_burger::before {
  -webkit-transform: rotate(45deg) translate(35px, -35px);
          transform: rotate(45deg) translate(35px, -35px);
}
.menu-btn.open .menu-btn_burger::after {
  -webkit-transform: rotate(-45deg) translate(35px, 35px);
          transform: rotate(-45deg) translate(35px, 35px);
}
.mainbody-container {
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  display: flex;
  flex-direction: column;
  padding: 2em;
  justify-content: center;
  background-color: ivory;
}
.mainbody-image {
  background-image: url(/mexi_chips/static/media/mexi_chips_cropped.08f8cace.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 600px;
}
.mainbody-button {
  width: 170px;
  height: 55px;
  position: absolute;
  display: block;
  background: transparent;
  border: 3px solid black;
  font-size: 1.5em;
  font-weight: 700;
  left: 13%;
  top: 77%;
  -webkit-transform: translate(50%, 50%);
          transform: translate(50%, 50%);
  text-decoration: none;
}
.yellow_logo_container {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.yellow_logo {
  width: 300px;
  padding: 2em;
}

.info-container {
  display: grid;
  grid-template-columns: 33% 33% 33%;
  text-align: center;
  grid-gap:3em;
  gap:3em;
  padding: 8em;
  background-color: ivory;
}

.location h3 {
  color: rgba(173, 4, 4, 0.918);
  padding: 2em;
  font-size: .9em;
}
.hours h3 {
  color: rgba(173, 4, 4, 0.918);
  padding: 2em;
  font-size: .9em;
}
.contact h3 {
  color: rgba(173, 4, 4, 0.918);
  padding: 2em;
  font-size: .9em;
  
}
.location p {
  word-spacing: .2em;
  font-family: 'Courier New', Courier, monospace;
}  
.hours p {
  word-spacing: .2em;
  font-family: 'Courier New', Courier, monospace;
}  
.contact p {
  word-spacing: .2em;
  font-family: 'Courier New', Courier, monospace;
}  

.about-container {
  width: 100%;
  display: flex;
  flex-direction:row;
  align-items: center;
  background-color: ivory;
  justify-content: space-around;
  /* padding: 2em; */
}
.about-picture {
  width: 30%;
}
.about-picture img {
  width: 100%;
  box-shadow: 2px 2px black;
}
.about-text {
  text-align: center;
  width: 50%;
  padding: 2em;
}
.about-text h1 {
  padding: .5em;
  font-family: Great Vibes;
  font-size: 3em;
  font-style: italic;
}
.about-text p {
  text-align: left;
}
.gallery-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  background-color: ivory;
  
}
.gallery-h1 {
  text-align: center;
  font-family: Great Vibes;
  font-size: 1.5em;
  padding: 2em;
}
.img-container {
  width: 90%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  flex-wrap: wrap;
  padding: 1em;

}

.img-container img { 
  width: 90%
}

.gallery-img  {
  width: 100%;
  padding: .5em;
}





@media screen and (max-width: 960px) {
  .NavbarItems {
      position: relative;
  }

  .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100vh;
      position: absolute;
      top: 68px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
  }

  .nav-menu.active {
      background:black;
      left: 0;
      top: 68px;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
  }

  .nav-links {
      text-align: center;
      padding: 2rem;
      width: 82%;
      display: table;
  }
  
  .menu-btn {
    display: block;
    position: absolute;
    top: -17px;
    right: -79px;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 2.8rem;
    cursor: pointer;
  }

  .nav-links-mobile {
      display: block;
      text-align: center;
      padding: 1.5rem;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      background: #1888ff;
      text-decoration: none;
      color: #fff;
      font-size: 1.5rem;
  }

  button {
      display: none;
  }
}
